.form-container {

    margin:auto;
    form{
        padding: 1rem;
    }
    .form-button {
        display: flex;
        justify-content: center;
    }
    .form-select{
        width: 100%;
        padding: 0.5rem;
        border: none;
        border-bottom: 1px solid black;
        outline: none;
        background-color: white;
    }
    .select-label{
        padding-bottom: 10px;
        padding-left: 5px;
        font-size: 16px;
    }
    .button-go-submit{
        color:white;
        background-color: black;
        width: 100%;
        padding: 10px;
        margin-top: 16px;
        outline: none;

        &:hover{
            color: black;
            background-color: white;
            cursor: pointer;
            border: solid 1px black;
        }

    }
}